import React,{Component}from 'react';
import '../css/direccion.css';
import Collapsepage from './Collapsepage'

class Direccion extends Component {
  constructor (){
    super();
    this.state={
    ubic : null
    }
  }
  componentWillMount(){
    fetch('https://image.maps.api.here.com/mia/1.6/mapview?c=19.404930,-99.169056&z=14&app_id=uXKmufo4aXIXBS5nw4HA&app_code=kQg_zysltomrOggVDhzuvw')
      .then(response => response)
      .then(ub =>this.setState({ubic: ub}))
    
  }
  render() {
    if(this.state.ubic){
      return(
        <div className="">
          <img className="responsive" src={this.state.ubic.url} alt='mapa'/>
          <Collapsepage />
        </div>
      )
    }else{
    return (
      <div className="App">
        <p>no hay estado</p>
      </div>
    );
  }
  }
}
export default Direccion;