import React from 'react';import { library } from '@fortawesome/fontawesome-svg-core'
import '../css/cancelar.css';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faPencilAlt } from '@fortawesome/free-solid-svg-icons'
import firebase from 'firebase';

library.add(faPencilAlt)

const firestore = firebase.firestore();
const settings = {/* your settings... */ timestampsInSnapshots: true};
firestore.settings(settings);
const db = firebase.firestore();


class Cancelar extends React.Component  {

    constructor(){
        super();

        this.handleChangePost = this.handleChangePost.bind(this);
        this.buttonPost = this.buttonPost.bind(this);
        
        this.state = {
          post:""
        }
      }




      handleChangePost(e){
        const { value, name } = e.target;
      
        this.setState({
         [name]: value 
      
         
        }) 
      
      }


      buttonPost(){


        

        db.collection("users").add({
           
            post: this.state.post
          
            
        })
        .then((docRef)=> {
            console.log("Document written with ID: ", docRef.id);
            //console.log(this.props.user.uid)
            document.getElementById("textarea").value="";
         
            

        
           
        })
        .catch((error)=>{
            console.error("Error adding document: ", error);
        });

   
    
 }


 
      

  render() {
    return(

      <div ><div className="container-fluid" id="card-social">
      <div className="card border"  >
      <div className="card-header" id="card-border"></div>
      <div className="card-body">
      <div className="form-group text-left">
      <label><b className="mr-1"></b>Describe brevemente el motivo de la inasistencia</label>
      <textarea id="textarea" rows="10" cols="50" value={this.state.post} onChange={this.handleChangePost} name='post' className="form-control" />
      <center>
      <button  onClick={this.buttonPost} type="button" className="btn btn-raised btn-secondary btn-sm mt-3" id="button-topost">
        Confirmar  <FontAwesomeIcon icon="pencil-alt" /></button>  
        </center>
      </div>
      </div>
      </div>
      
      </div>     
        
</div>




    );
  }
};

export default Cancelar;