import React, {Fragment} from 'react';
import logo from '../img/bus.png'
import '../css/homeFamily.css'
import firebaseApp from '../config/firebase' 
import { BrowserRouter,Link,Route} from 'react-router-dom'
import Calenda from '../components/calendar' 
import Cancelar from '../components/cancelar' 
import Direccion from '../components/direccion'
import Pagos from '../components/pagos'
import { Navbar, NavbarBrand, NavbarNav, NavbarToggler, Collapse, NavItem, NavLink} from 'mdbreact';


class Nav extends React.Component {
  constructor(props) {
      super(props);
      this.state = {
          collapse: false,
      };
  this.onClick = this.onClick.bind(this);
}
onClick(){
  this.setState({
      collapse: !this.state.collapse,
    });
}

logout(){
  firebaseApp.auth().signOut()
  .then(function() {
      console.log('Saliendo...');
    })
    .catch(function(error) {
      console.log(error);
    });
}


render() {

    return (
        
          <BrowserRouter>
          <Fragment>
          
            <Navbar id="nav" dark expand="md" scrolling>
                <NavbarBrand href="/">
                <img width="60px"  src={logo} alt="logo"/>  <span id="titule2">Here bus</span>
                
                </NavbarBrand>
                <NavbarToggler onClick={this.onClick } />
                <Collapse isOpen = { this.state.collapse } navbar>
                    <NavbarNav left id="tituleNav">
                      <NavItem active>
                         
                      </NavItem>
                      <NavItem>
                              <NavLink to="#">
                              <Link id="titule3" to='/Direccion'>
                              Ruta
                              </Link>
                               </NavLink>
                          </NavItem>

                          <NavItem>
                              <NavLink to="#">
                              <Link id="titule3" to='/Calendario'>
                              Calendario
                              </Link>
                               </NavLink>
                          </NavItem>

                          <NavItem>
                              <NavLink to="#">
                              <Link id="titule3" to='/Cancelar asistencia'>
                              Cancelar asistencia 
                              </Link>
                               </NavLink>
                          </NavItem>

                   
                          <NavItem>
                              <NavLink to="#">
                              <Link id="titule3" to='/Pagos'>
                              Pagos
                              </Link>
                               </NavLink>
                          </NavItem>

                    </NavbarNav>
                    <NavbarNav right >
                      
                      <NavItem>
                      <NavLink onClick={this.logout} to="#" id="titule3" >Cerrar Sesión </NavLink>
                      </NavItem>
                    </NavbarNav>
                </Collapse>
            </Navbar>
       

  
       
       <Route path='/Direccion' component={Direccion} />
       <Route path='/Calendario' component={Calenda} />
       <Route exact path='/Cancelar asistencia' component={Cancelar} />
       <Route exact path='/Pagos' component={Pagos} />



​
​     
         
</Fragment>
</BrowserRouter>
    );
  }
}

export default Nav;